@font-face {
  font-family: 'New York';
  src: url("/public/NewYork.otf") format("opentype");
}

html {
  background: #090a0d;
  color: white;
  font-family: "New York", "Times New Roman", serif;
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  display: flex;
}

.side-panel {
  background: #0c0f14;
  width: 200px;
  height: 100vh;
  padding: 8px;
}

.side-panel .side-panel-item {
  padding: 8px;
  cursor: pointer;
  transition: 0.5s;
}

.side-panel .side-panel-item:hover {
  color: #1951b5;
  padding-left: 16px;
}

.contents {
  flex-grow: 1;
  padding: 16px;
}